.logo_area {
  display: flex;
  background-color: #feda00;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  height: 60px;
}

.logo_area img {
  max-height: 50px;
  margin-right: 3px;
}

.logo_area span {
  font-size: 26px;
  font-weight: 700;
  color: #012970;
  font-family: "Nunito", sans-serif;
}

.menu_area {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 10px;
}

.node {
  color: white;
  font-size: 20px;
  font-weight: 600;
  padding: 5px 10px;
  margin: 10px 0;
  cursor: pointer;
  transition: color 0.3s, background-color 0.3s;
}

.node:hover {
  color: #333;
  background-color: #f2f2f2;
  border-radius: 4px;
}

.version_area {
  margin-top: auto;
  text-align: center;
  padding: 10px 0;
  color: white;
  font-size: 12px;
}

.burger_btn_close {
  left: 0;
}
